;EAC/B,0BAA0B;AAC5B;AACA;EACE,+BAA+B;EAC/B,0BAA0B;AAC5B;AACA,kEAAkE;AAClE;EACE,yBAAyB;EACzB,yBAAyB;AAC3B;AACA;EACE,gBAAgB;EAChB,eAAe;EACf,iJAAiJ;EACjJ,oJAAoJ;EACpJ,+IAA+I;EAC/I,4IAA4I;AAC9I;AACA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,oJAAoJ;EACpJ,uJAAuJ;EACvJ,kJAAkJ;EAClJ,+IAA+I;AACjJ;AACA;EACE,yBAAyB;AAC3B;AACA;EACE,yBAAyB;AAC3B;AACA;EACE,YAAY;EACZ,WAAW;EACX,cAAc;EACd,uEAAsE;EACtE,6BAA6B;AAC/B;AACA;EACE,8BAA8B;AAChC;AACA;EACE,8BAA8B;AAChC;AACA,wCAAwC;AACxC;;EAEE,yBAAyB;EACzB,yBAAyB;EACzB,2CAA2C;;AAE7C;AACA;EACE,+BAA+B;AACjC;AACA;EACE,+BAA+B;AACjC;AACA;EACE,+BAA+B;AACjC;AACA,gBAAgB;AAChB;EACE,YAAY;AACd;AACA;EACE,gCAAgC;EAChC,oBAAoB;AACtB;AACA;EACE,wCAAwC;EACxC,YAAY;EACZ,YAAY;EACZ,oCAAoC;EACpC,2DAA2D;EAC3D,wDAAwD;EACxD,mDAAmD;EACnD,iCAAiC;EACjC,8BAA8B;EAC9B,yBAAyB;EACzB,uBAAuB;EACvB,kBAAkB;EAClB,MAAM;EACN,8CAA8C;;EAE9C,wBAAwB;EACxB,qBAAqB;EACrB,gBAAgB;EAChB,mEAAsD;EACtD,4JAA4J;EAC5J,+JAA+J;EAC/J,0JAA0J;EAC1J,uJAAuJ;AACzJ;AACA;EACE,yBAAyB;EACzB,yBAAyB;EACzB,YAAY;EACZ,iCAAiC;EACjC,8BAA8B;EAC9B,yBAAyB;AAC3B;AACA;EACE,qBAAqB;AACvB;AACA;EACE,yBAAyB;EACzB,YAAY;EACZ,iCAAiC;EACjC,8BAA8B;EAC9B,yBAAyB;AAC3B;AACA;EACE,YAAY;EACZ,yBAAyB;EACzB,yBAAyB;AAC3B;AACA;EACE,qBAAqB;EACrB,sBAAsB;AACxB;AACA;EACE,YAAY;EACZ,0BAA0B;;AAE5B;AACA;;GAEE,sBAAuB;AACzB","sourcesContent":["/* TabContainer \n * \n * Styling TabContainer means styling the TabList and Its content container  (dijitTitlePane)\n * \n * Tab List: (including 4 kinds of tab location)\n * \t\t.dijitTabContainerTop-tabs     - tablist container at top\n * \t\t.dijitTabContainerBottom-tabs  - tablist container at bottom\n * \t\t.dijitTabContainerLeft-tabs    - tablist container at left\n * \t\t.dijitTabContainerRight-tabs   - tablist container at right\n * \n * Tab Strip Button:\n * \t\t.dijitTabStripIcon - tab strip button icon\n * \t\t.dijitTabStripMenuIcon - down arrow icon position\n * \t\t.dijitTabStripSlideLeftIcon - left arrow icon position\n * \t\t.dijitTabStripSlideRightIcon - right arrow icon position\n * \n * \t\t.tabStripButtonDisabled - styles for disabled tab strip buttons\n * \n * Tab Button:\n * \t\t.dijitTabContainerTop-tabs .dijitTab     - styles for top tab button container\n * \t\t.dijitTabContainerBottom-tabs .dijitTab  - styles for bottom tab button container\n * \t\t.dijitTabContainerLeft-tabs .dijitTab    - styles for left tab button container\n * \t\t.dijitTabContainerRight-tabs .dijitTab   - styles for right tab button container\n * \n * \t\t.dijitTabContainerTop-tabs .dijitTabChecked .dijitTab\n * \t\t\t\t- styles for selected status of top tab button\n * \t\tsame to Bottom, Left, Right Tabs\n * \n * \t\t.dijitTabHover .dijitTab   - styles when mouse hover on tab buttons\n * \t\t.dijitTabActive .dijitTab  - styles when mouse down on tab buttons\n * \t\t.dijitTabChecked .dijitTab  - styles when on buttons of selected tab\n * \n * \t\t.dijitTabCloseButton - the close action buttons lie at the right top of each tab button on closable tabs\n * \t\t.dijitTabCloseButtonHover - styles when mouse hover on close action button\n * \t\t.dijitTabCloseButtonActive - styles when mouse down on close action button\n * \n * Tab Button: (checked status)\n * \n * Tab Content Container:\n * \t\t.dijitTabContainerTop-dijitContentPane\n * \t\t.dijitTabContainerBottom-dijitContentPane\n * \t\t.dijitTabContainerLeft-dijitContentPane\n * \t\t.dijitTabContainerRight-dijitContentPane - for background and padding\n * \n * Nested Tabs:\n * \t\t.dijitTabContainerNested - Container for nested tabs\n * \t\t.dijitTabContainerTabListNested - tab list container for nested tabs\n */\n/*** some common features ***/\n.claro .dijitTabPaneWrapper {\n  background: #ffffff;\n}\n.claro .dijitTabPaneWrapper,\n.claro .dijitTabContainerTop-tabs,\n.claro .dijitTabContainerBottom-tabs,\n.claro .dijitTabContainerLeft-tabs,\n.claro .dijitTabContainerRight-tabs {\n  /* todo: add common class name for this div */\n\n  border-color: #b5bcc7;\n}\n.claro .dijitTabCloseButton {\n  background: url(\"../layout/images/tabClose.png\") no-repeat;\n  width: 14px;\n  height: 14px;\n  margin-left: 5px;\n  margin-right: -5px;\n}\n.claro .dijitTabCloseButtonHover {\n  background-position: -14px;\n}\n.claro .dijitTabCloseButtonActive {\n  background-position: -28px;\n}\n.claro .dijitTabSpacer {\n  /* set the spacer invisible.  note that height:0 doesn't work on IE/quirks, it's still 10px. */\n\n  display: none;\n}\n.claro .dijitTab {\n  border: 1px solid #b5bcc7;\n  background-color: #efefef;\n  -webkit-transition-property: background-color, border;\n  -moz-transition-property: background-color, border;\n  transition-property: background-color, border;\n  -webkit-transition-duration: 0.35s;\n  -moz-transition-duration: 0.35s;\n  transition-duration: 0.35s;\n  color: #494949;\n}\n.claro .dijitTabHover {\n  border-color: #759dc0;\n  background-color: #abd6ff;\n  -webkit-transition-duration: 0.25s;\n  -moz-transition-duration: 0.25s;\n  transition-duration: 0.25s;\n  color: #000000;\n}\n.claro .dijitTabActive {\n  border-color: #759dc0;\n  background-color: #7dbdfa;\n  color: #000000;\n  -webkit-transition-duration: 0.1s;\n  -moz-transition-duration: 0.1s;\n  transition-duration: 0.1s;\n}\n.claro .dijitTabChecked {\n  border-color: #b5bcc7;\n  background-color: #cfe5fa;\n  color: #000000;\n}\n.claro .dijitTabDisabled {\n  background-color: #d3d3d3;\n}\n.claro .tabStripButton {\n  background-color: transparent;\n  border: none;\n}\n/*** end common ***/\n/*************** top tab ***************/\n.claro .dijitTabContainerTop-tabs .dijitTab {\n  /* unselected (and not hovered/pressed) tab */\n\n  top: 1px;\n  /* used for overlap */\n\n  margin-right: 1px;\n  padding: 3px 6px;\n  border-bottom-width: 0;\n  min-width: 60px;\n  text-align: center;\n  background-image: url(\"images/tabTopUnselected.png\");\n  background-repeat: repeat-x;\n  background-image: -moz-linear-gradient(top, #ffffff 0px, #ffffff 1px, rgba(255, 255, 255, 0.1) 2px, rgba(255, 255, 255, 0.6) 7px, rgba(255, 255, 255, 0) 100%);\n  backgroun